<template>
	<div id='groupChatManage'>
		<div class="top">
			<div class="topLeft">
				<el-input class="selectInput" v-model="huahaiNumber" placeholder="群名"></el-input>

				<el-input class="selectInput" v-model="userName" placeholder="群主"></el-input>

				<el-date-picker
				class="selectInput"
				v-model="startTime"
				type="daterange"
				range-separator="至"
				start-placeholder="开始日期"
				end-placeholder="结束日期">
				</el-date-picker>

				<el-input class="selectInput" v-model="userName" placeholder="状态"></el-input>

				<div class="selectInput" style="display: flex;justify-content: space-between">
					<el-dropdown split-button type="primary" @command="handleCommand" >
						{{selectCommand}}
						<el-dropdown-menu slot="dropdown">
							<el-dropdown-item command="10-50人">10-50人</el-dropdown-item>
							<el-dropdown-item command="51-100人">51-100人</el-dropdown-item>
							<el-dropdown-item command="101-200人">101-200人</el-dropdown-item>
							<el-dropdown-item command="201-300人">201-30人</el-dropdown-item>
						</el-dropdown-menu>
					</el-dropdown>

					<el-button type="primary" style="width:100px">查   询</el-button>
				</div>
			</div>
		</div>

		<div class="bottom">
			<el-table
			:data="tableData"
			height="1080"
			border
			lazy
			row-key="id"
			size="mini"
			:tree-props="{children: 'children', hasChildren: 'hasChildren'}">
				<el-table-column
					class="tableClass"
					align='center'
					prop="id"
					label="群名称"
					width="180">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="name"
					label="群主名称">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount1"
					label="人数">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount2"
					label="创建时间">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					sortable
					label="状态">
				</el-table-column>
				<el-table-column
					class="tableClass"
					align='center'
					prop="amount3"
					label="详情">
					<template slot-scope="scope">
						<el-button @click="handleClick(scope.row)" type="text" size="small">详细信息</el-button>
					</template>
				</el-table-column>
			</el-table>
		</div>

		<div class="pagination">
			<pagination @handleSizeChange="handleSizeChange" @handleCurrentChange="handleCurrentChange" :total="50"></pagination>
		</div>

		<el-dialog title="详细信息" :visible.sync="dialogFormVisible">
			<h3>华海号</h3>
			<el-input
			placeholder="华海号"
			v-model="dialog.id"
			:disabled="true">
			</el-input>

			<h3>用户昵称</h3>
			<el-input
			placeholder="用户昵称"
			v-model="dialog.name"
			:disabled="true">
			</el-input>

			<h3>企业名称</h3>
			<el-input
			placeholder="企业名称"
			v-model="dialog.amount1"
			:disabled="true">
			</el-input>

			<h3>信用代码</h3>
			<el-input
			placeholder="信用代码"
			v-model="dialog.amount2"
			:disabled="true">
			</el-input>

			<h3>提交时间</h3>
			<el-input
			placeholder="提交时间"
			v-model="dialog.amount3"
			:disabled="true">
			</el-input>

			<h3>状态</h3>
			<el-input
			placeholder="状态"
			v-model="dialog.amount3"
			:disabled="true">
			</el-input>

			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false;selectedOptions = []">关 闭</el-button>
			</div>

			 <el-cascader
			:options="areaSelectData"
			@change="handleChange"
			class="full-width"
			size="large"
			v-model="selectedOptions"
			placeholder="请选择您所在的城市"
			/>
		</el-dialog>
	</div>
</template>

<script>

import { regionData, CodeToText } from "element-china-area-data";
import pagination from "../../../../components/pagination/index.vue"
export default {
	name:'groupChatManage',
	components:{
		pagination
	},
	data(){
		return{
			dialog:'',
			huahaiNumber:'',
			userName:'',
			userId:'',
			phoneNumber:'',
			state:'',
			startTime:'',
			endTime:'',
			dialogFormVisible:false,
			areaSelectData:regionData,
			selectedOptions:[],
			selectCommand:'10-50人',
			tableData: [
				{
					id: '12987123',
					name: '王小虎',
					amount1: '165',
					amount2: '4.43',
					amount3: 12
				},
				{
					id: '12987124',
					name: '王小虎',
					amount1: '324',
					amount2: '1.9',
					amount3: 9
				},
				{
					id: '12987125',
					name: '王小虎',
					amount1: '621',
					amount2: '2.2',
					amount3: 17
				}
			]
		}
	},

	created(){

	},

	methods:{
		handleClick(row){
			this.dialog = row;
			this.dialogFormVisible = !this.dialogFormVisible;
		},
		handleSizeChange(val) {
			console.log(`每页 ${val} 条`);
		},
		handleCurrentChange(val) {
			console.log(`当前页: ${val}`);
		},

		handleChange() {
			let loc = "";
			for (let i = 0; i < this.selectedOptions.length; i++) {
				loc += CodeToText[this.selectedOptions[i]];
			}
		},

		handleCommand(command){
			this.selectCommand = command;
		},
	}
}
</script>

<style scoped>
	#groupChatManage{
		box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
		padding: 20px;
		margin-top: 20px;
	}

	.top{
		margin-top:-20px;
		height:60px;
		display: flex;
		justify-items: space-between;
	}

	.bottom{
		overflow: scroll;
	}

	.topLeft{
		display: flex;
		flex-wrap: wrap;
	}

	.selectInput{
		width: 250px !important;
		margin-right: 30px;
		margin-top: 10px;
		height:40px;
	}

	.pagination{
		width: 100%;
		display: flex;
		justify-content: center;
		margin-top: 10px;
	}

</style>
